<template>
  <!-- v-scroll="handleScroll" -->
  <div class="recommend" v-show="recommendVisible">
    <div class="container">
      <div class="title">{{ $t('translateLang.index.text1')}}</div>
      <div class="title-tips">{{ $t('translateLang.index.text2')}}</div>
      <div class="box">
        <img src="static/images/pre.png" alt="" class="pre"  @click="handlePreCard()" :style="{'cursor': preCursor }">
        <div class="card-carouse">
          <ul :style="{ 'width': (commendTotal + 3) * 300 + 'px', 'left': left + 'px' }">
            <li v-for="item in commendList" :key="item.id">
              <div class="clearfix">
                <div class="grid-content bg-purple pull-left">
                  <div class="photo-img-container" style="display: inline-block;"><img :src="item.head_img"  class="name-short" alt=""> <i class="online-state online-state-green" v-if="item.is_online_status == 1"></i><i class="online-state online-state-gray" v-if="item.is_online_status == 0"></i></div>
                </div>
                <div class="name pull-left">{{ item.nickname }}</div>
              </div>
              
              <div class="des">
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text3')}}</div><div class="pull-right">{{ item.trade_count_num }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text4')}}</div><div class="pull-right">{{ parseFloat(item.thumb_up_rate).toFixed(4) * 100 }}%</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text5')}}</div><div class="pull-right">{{ item.trade_trust_num }}</div></div>
                
                <div class="pay-way clearfix"><div class="pull-left">{{ $t('translateLang.index.text6')}}</div><img :src="image.pay_logo" alt="" v-for="image in item.pay_type_list" :key="image.id" class="pull-right"></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text7')}}</div><div class="pull-right">{{ item.mix_transaction_price }}~{{ item.max_transaction_price }} {{ item.legal_mark }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text8')}}</div><div class="pull-right">{{ item.trade_num }} {{ item.currency_mark }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text9')}}</div><div class="pull-right"><span class="price">{{ item.transaction_price }} </span><span class="price-mark">{{ item.legal_mark }}</span></div></div>
                <el-button class="buy-btn" v-if="item.ad_type == 0" @click.native="handleTrade(item.ad_id, item.ad_type)">{{ $t('translateLang.index.text10')}} {{ item.currency_mark }}</el-button>
                <el-button class="sell-btn" v-if="item.ad_type == 1" @click.native="handleTrade(item.ad_id, item.ad_type)">{{ $t('translateLang.index.text11')}} {{ item.currency_mark }}</el-button>
              </div> 
            </li>
            <li v-for="(item, index) in commendList" :key="item.id" v-if="index < 3">
              <div class="clearfix">
                <div class="grid-content bg-purple pull-left">
                  <div class="photo-img-container" style="display: inline-block;"><img :src="item.head_img"  class="name-short" alt=""> <i class="online-state online-state-green" v-if="item.is_online_status == 1"></i><i class="online-state online-state-gray" v-if="item.is_online_status == 0"></i></div>
                </div>
                <div class="name pull-left">{{ item.nickname }}</div>
              </div>
              
              <div class="des">
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text3')}}</div><div class="pull-right">{{ item.trade_count_num }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text4')}}</div><div class="pull-right">{{ item.thumb_up_rate * 100 }}%</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text5')}}</div><div class="pull-right">{{ item.trade_trust_num }}</div></div>
                
                <div class="pay-way clearfix"><div class="pull-left">{{ $t('translateLang.index.text6')}}</div><img :src="image.pay_logo" alt="" v-for="image in item.pay_type_list" :key="image.id" class="pull-right"></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text7')}}</div><div class="pull-right">{{ item.mix_transaction_price }}~{{ item.max_transaction_price }} {{ item.legal_mark }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text8')}}</div><div class="pull-right">{{ item.trade_num }} {{ item.currency_mark }}</div></div>
                <div class="clearfix"><div class="pull-left">{{ $t('translateLang.index.text9')}}</div><div class="pull-right"><span class="price">{{ item.transaction_price }} </span><span class="price-mark">{{ item.legal_mark }}</span></div></div>
                <el-button class="buy-btn" v-if="item.ad_type == 0" @click.native="handleTrade(item.ad_id, item.ad_type)">{{ $t('translateLang.index.text10')}} {{ item.currency_mark }}</el-button>
                <el-button class="sell-btn" v-if="item.ad_type == 1" @click.native="handleTrade(item.ad_id, item.ad_type)">{{ $t('translateLang.index.text11')}} {{ item.currency_mark }}</el-button>
              </div> 
            </li> 
          </ul>
        </div>
        <img src="static/images/next.png" alt="" class="next" @click="handleNextCard()" :style="{'cursor': nextCursor }">
      </div>
      
    </div>
    
  </div>
</template>

<script>
  import qs from 'qs'
  import { userInfo } from '@/api/user'
  import { targetEnvironment } from '@/utils/utils'
  import { getRecommendAdList } from '@/api/index'
  export default {
    data() {
      return {
        commendTotal: 0,
        left: 0,
        commendList: [],
        nextCursor: 'pointer',
        preCursor: 'pointer',
        recommendVisible: false,
        userInfo: {},
        href: targetEnvironment()
      }
    },
    created() {
      this.initRecommend()
      this.initUserInfo()
    },
    methods: {
      handleCrossEnvHref(object) {
        const userInfo = window.localStorage.getItem('link_userInfo')
        const token = !userInfo ? '' : JSON.parse(userInfo).token
        if (!object) {
          if (!token) {
            return this.href
          } else {
            return this.href + '/?token=' + encodeURIComponent(token)
          }
        } else {
          const env = this.href
          const path = object.path
          const queryStr = qs.stringify(object.query)
          const query = queryStr ? '?' + queryStr : ''
          let href = env + path + query
          if (!token) {
            return href
          } else {
            href = queryStr
              ? href + '&token=' + encodeURIComponent(token)
              : href + '?token=' + encodeURIComponent(token)
            // console.log('href', href)
            return href
          }
        }
      },
      initRecommend() {
        getRecommendAdList().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.commendTotal = res.data.list_total
          this.commendList = [...res.data.list_data]
          if (this.commendTotal > 4) {
            this.recommendVisible = true
          }
        }).catch(() => {
        })
      },
      handlePreCard() {
        if (this.left < 0) {
          this.left = this.left + 300
        }
      },
      handleNextCard() {
        if (Math.abs(this.left) <= this.commendTotal * 300 - 600) {
          this.left = this.left - 300
        } else {
          this.left = 0
        }
      },
      initUserInfo() {
        if (window.localStorage.getItem('link_userInfo')) {
          userInfo().then(res => {
            if (res.status !== 1000) {
              return false
            }
            this.userInfo = res.data
            if (parseFloat(this.userInfo.is_set_double_certification) === 0) {
              this.ruleForm.pass_double = '0'
            }
          }).catch(() => {
          })
        }
      },
      handleTrade(ad_id, ad_type) {
        /*
        // 先验证登录再校验实名与否
        if (this.$store.state.is_login) {
          if (this.userInfo.status !== 1) {
            this.$message({
              message: this.$t('translateLang.tradeDetail.text71'),
              duration: 1500,
              type: 'warning'
            })
            return false
          }
          if ((this.userInfo.is_pwdtrade_exists).toString() !== '1') {
            this.$message({
              message: this.$t('translateLang.tradeDetail.text72'),
              duration: 1500,
              type: 'warning'
            })
            return false
          }
          const href = this.handleCrossEnvHref({
            path: '/sellBuy',
            query: {
              ad_id: ad_id,
              require_type: ad_type
            }
          })
          window.location.href = href
        } else {
          this.$message({
            message: this.$t('translateLang.tradeDetail.text73'),
            duration: 1500,
            type: 'warning'
          })
        }
        */
        if (!this.userInfo.token) {
          this.$message({
            message: '请先登录',
            duration: 1500,
            type: 'warning'
          })
          return false
        }
        if (this.userInfo.status !== 1) {
          this.$message({
            message: this.$t('translateLang.tradeDetail.text71'),
            duration: 1500,
            type: 'warning'
          })
          return false
        }
        if ((this.userInfo.is_pwdtrade_exists).toString() !== '1') {
          this.$message({
            message: this.$t('translateLang.tradeDetail.text72'),
            duration: 1500,
            type: 'warning'
          })
          return false
        }
        const href = this.handleCrossEnvHref({
          path: '/sellBuy',
          query: {
            ad_id: ad_id,
            require_type: ad_type
          }
        })
        window.location.href = href
      },
      handleScroll: function(evt, el) {
        if (window.scrollY > (el.offsetTop - el.offsetHeight - 150)) {
          el.setAttribute(
            'style',
            'opacity: 1; transform: translate3d(0, 10px, 0)'
          )
        }
        return window.scrollY > (el.offsetTop - el.offsetHeight - 100)
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .recommend {
    background: white;
    position: relative;
    padding: 40px 0 90px;
    .pre{
      position: absolute;
      left: -50px;
      top: 156px;
      cursor: pointer;
      @media (max-width: 1200px) {
        display: none;
      }
    }
    .next{
      position: absolute;
      right: -50px;
      top: 156px;
      cursor: pointer;
      @media (max-width: 1200px) {
        display: none;
      }
    }
    .box{
      position: relative;
    }
    .card-carouse{
      width: 1200px;
      height: 430px; 
      overflow: hidden;
      position: relative;
      ul{
        display: flex;
        position: relative;
        font-size: 12px;
        position: absolute;
        overflow: hidden;
        transition: all 0.3s;
        left: 0;
        li{
          margin: 10px 15px 10px 10px;
          box-sizing: border-box;
          width: 275px;
          margin-right: 15px;
          border: 1px solid white;
          box-shadow: 0px 0px 13px 2px rgba(187, 189, 194, .3);
          border-radius: 8px;
          padding: 24px;
          transition: all 0.3s;
          cursor: pointer;
          &:hover{
            // border: 1px solid #a1d3fd;
            transform: translate3d(0,0,0);
            transform: scale(1.03)
          }
        }
      }
    }
    
    .name{
      font-size: 18px;
      color: #5d667a;
      width: 170px;
      overflow: hidden;
      white-space: nowrap;
      text-overflow:ellipsis;
    }
    .des{
      width: 230px;
      text-align: left;
      margin: 0 auto;
      font-size: 14px;
      >div{
        margin-bottom: 5px;
        color: #8e939f;
        span{
          font-size: 14px;
          color: #4d576e;
        }
      }
      .pull-right{
        color: #4d576e;
      }
      .pay-way{
        margin-bottom: 25px;
        img{
          margin-right: 5px;
          // float: left;
          width: 20px;
        }
      }
      .buy-btn,
      .sell-btn{
        margin-top: 20px;
        width: 100%;
        padding: 0;
        height: 35px;
      }
    }
    .photo-img-container{
      width: 51px;
      height: 51px;
      position: relative;
      .name-short{
        border-radius: 41px;
        width: 41px;
        height: 41px;
        line-height: 41px;
        font-size: 18px;
        color: white;
      }
      .online-state{
        position: absolute;
        right: 7px;
        bottom: 3px;
        width: 8px;
        height: 8px;
        border-radius: 8px;
        border: 2px solid #f3fcf5;
      }
      .online-state-green{
        background: #5ddb77;
      }
      .online-state-gray{
        background: #c4ced8;
      }
    }
    .price{
      font-size: 18px !important;
      color: #4d576e;
      font-weight: bold
    }
    .price-mark{
      font-weight: bold;
    }
    .sell-btn, .sell-btn:hover, .sell-btn:active, .sell-btn:focus{
      background: #3e77b3;
    }
    .buy-btn, .buy-btn:hover, .buy-btn:active, .buy-btn:focus{
      background: #1fa8f6;
    }
  }
</style>



